//header stylings
.footer {
    position: fixed;
    bottom: 0;
    width: 100%;
    left: 0;
    right: 0;
    background-color: transparent;
    padding: 20px;

    @include bp('tab') {
        padding: 30px 50px;
    }
}

.footer-copyright {
    opacity: 0.6;
    color: #333333;
    font-family: $lato;
    font-size: 12px;
    font-weight: 400;
    position: relative;

    &::before {
        position: absolute;
        content: '';
        bottom: -20px;
        width: 50px;
        height: 4px;
        left: 50%;
        z-index: 2;
        transform: translateX(-50%);
        background-color: #d19e66;
        margin: 0 0 0 -25px;
        
        @include bp('tab'){
            bottom: -30px;
        }
    }
    
    &::after {
        position: absolute;
        content: '';
        bottom: -20px;
        width: 50px;
        height: 4px;
        left: 50%;
        margin: 0 0 0 25px;
        transform: translateX(-50%);
        background-color: #bac371;
        
        @include bp('tab'){
            bottom: -30px;
        }
    }
}